.keyboard {
	width: 100%;
	max-width: 45em;
	margin: 0 auto;
	background: #dadada;
	-webkit-box-shadow: 0 2px 12px 0 rgba(0, 0, 0, .5);
	        box-shadow: 0 2px 12px 0 rgba(0, 0, 0, .5);
	font-size: 18px;
}

.keyboard-row {
	display: -ms-flexbox;
	display: flex;
}

.keyboard-keysSet {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-positive: 1;
	    flex-grow: 1;
}

.keyboard-button {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-preferred-size: 5.5em;
	    flex-basis: 5.5em;
	height: 2.5em;
	margin: 5px;
	border-radius: 4px;
	background-color: #f5f5f5;
	border: 1px solid #cecece;
	font-size: 1em;
	font-weight: 300;
}

.keyboard-button:focus {
	outline: none;
}

.keyboard-button:disabled {
	opacity: .4;
	cursor: default;
}

.keyboard-button:active {
	background-color: #ccc;
}

.keyboard-number-button {
	-ms-flex-positive: 1;
	    flex-grow: 1;
}

.keyboard-half-button {
	-ms-flex-preferred-size: 2.75em;
	    flex-basis: 2.75em;
}

.keyboard-space,
.keyboard-space-button{
	-ms-flex-positive: 1;
	    flex-grow: 1;
}

.keyboard-util-button {
	-ms-flex-positive: 2;
	    flex-grow: 2;
	max-width: 3em;
}

.keyboard-additional-button {
	-ms-flex-preferred-size: 7em;
	    flex-basis: 7em;
}

.keyboard-submit-button {
	-ms-flex-preferred-size: 8em;
	    flex-basis: 8em;
	background-color: #63b324;
	border-color: #63b324;
	color: #fff;
}
.keyboard-skip-button {
	-ms-flex-preferred-size: 8em;
	    flex-basis: 8em;
	background-color: #b32424;
	border-color: #b32424;
	color: #fff;
}

.numeric-keyboard {
	width: auto;
}

.numeric-keyboard .keyboard-button {
	width: 4em;
	-ms-flex-preferred-size: auto;
	    flex-basis: auto;
}

.numeric-keyboard .keyboard-submit-button {
	-ms-flex: 1 1;
	    flex: 1 1;
	width: auto;
}



